import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

// movie component -> state 필요하지 않음
// component가 state가 필요 없을 경우에는 굳이 class component가 될 필요없음 

function Movie ({id, year, title, summary, poster, genres}) {
    return ( 
        <div className="movie">
            <img src={poster} alt={title} title={title}/>
            <div className="movie__data">
                <h3 className="movie__title" style={{backgroundColor: ""}}> {title} </h3>
                <h5 className="movie__year"> {year} </h5>
                <ul className="movie__genres"> 
                    {genres.map((genre, index) => <li key={index} className="genres__genre">{genre}</li>)} 
                </ul>
                <p className="movie_summary"> {summary} </p>
            </div>
        </div> 
    )
}

Movie.propTypes = {
    id : PropTypes.number.isRequired,
    year : PropTypes.number.isRequired,
    title : PropTypes.string.isRequired,
    summary : PropTypes.string.isRequired,
    poster : PropTypes.string.isRequired,
    genres : PropTypes.arrayOf(PropTypes.string).isRequired
    // medium-cover-image ->  url로 되어 있으므로 string
    // 위에 있는 것 모두 `isRequired`와 연결하여 prop가 제공되지 않았을 때
    // 경고가 보이도록 할 수 있습니다.
}

export default Movie;